.reg_project{
    margin: auto;
    display: inline;
    z-index: 5;
}

#sidebar{
    position: absolute;
    list-style-type: none;
    padding: 0;
    margin-left: -3%;
    margin-top: 20px;
    display: inline-block;
    width: 4%;
    z-index: 4;
}

.NavLink{
    position: relative;
    display: block;
    text-decoration: none;
    font-size: 14pt;
    margin: 2px 0;
    border-radius: 50px 0 0 50px;
    background-color: lightblue;
    cursor: pointer;
}

