#sidebar a:hover {
    margin-right: -5px;
    transform: translate(-5px);
}

.Project{
    width: 80%;
    margin: 0 auto;
}
.ProjectName{
    margin: auto;
    /* background-color: #e9f8dad0; */
    /* background-color: #ccff99; */
    background-color: white;
    /* border-top: 1px solid black; */
    box-shadow: none;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid black;
    outline: none;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    text-align: left;
    font-size: 15px;
}

.active, .ProjectName:hover {
    /* background-color: #e9f8dad0; */
}

.ProjectContent {
    padding: 0 18px;
    /* display: none; */
    overflow: hidden;
}

.Portrait {
    width: 28%;
    height: 225px;
    background: #ccff99;
    padding: 10px;
    display: inline-block;
    margin: 20px;
    /* box-shadow: 0px 0px 9px rgb(69, 102, 19);  */
    background: white;
    border: 1px solid white;
    border-radius: 10px;
    cursor: pointer;
    text-align: center;
    vertical-align: middle;
}

.GalleryImage {
    max-width: 100%;
    height: 100%;
    margin: auto;
    vertical-align: center;
}

.Portrait:hover{
    box-shadow: 0px 0px 9px black;
}

@supports(object-fit: scale-down){
    .Portrait {
        width: 28%;
        height: 15rem;
    }

    .GalleryImage{
        width: 100%;
        height: 100%;
        object-fit: scale-down;
        vertical-align: middle;
    }
}

@media only screen and (max-width: 1800px) {
    .Portrait{
        width: 40%;
        height: 250px;
    }
}

@media only screen and (max-width: 1100px) {
    .Portrait{
        width: 90%;
        height: 300px;
    }
  }

  @keyframes enlarge {
    from {
      height: 1vh;
    }
    to {
      height: 60vh; /* your line height here */
    }
  }

  .LargeImage {
      animation-duration: 0.5s;
      animation-name: enlarge;
      max-height: 60vh
  }